/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { button } = wp.components.Icon;
const icon = button;
/**
 * Internal dependencies
 */
import acfInitBlock from '../utils/init-block';
import edit from './edit';
import deprecated from './deprecated';
import metadata from './block.json';
import save from './save';

const { name } = metadata;

export { metadata, name };

export const settings = {
	icon,
	example: {
		attributes: {
			className: 'is-style-fill',
			text: __( 'Call to Action' ),
		},
	},
	edit,
	deprecated,
	save,
	merge: ( a, { text = '' } ) => ( {
		...a,
		text: ( a.text || '' ) + text,
	} ),
};

export const init = () => acfInitBlock( { name, metadata, settings } );